import { ref } from "vue";
import { Tabs, Tab } from 'gw-vue-ui';
export default {
    components: {
        GwTabs: Tabs,
        GwTab: Tab
    },
    setup() {
        const active = ref(2);
        const activeName = ref('b');
        const onClick = (name, title) => {
            console.log('点击tab索引值-名字', name, title);
        };
        const beforeChange = (index) => {
            // 返回 false 表示阻止此次切换
            if (index === 1) {
                console.log('阻止此次切换');
                return false;
            }
            // 返回 Promise 来执行异步逻辑
            return new Promise((resolve) => {
                // 在 resolve 函数中返回 true 或 false
                resolve(index !== 3);
            });
        };
        return { active, activeName, onClick, beforeChange };
    }
};
